/* ********************************************************** */
/* declaration of global variables starts */

:root {
  --pos-value: 0;

  /* font-colors and background colors */
  --dark-bg-color: #444a5a;
  --footer-dark-bg-color: #343434;
  --light-bg-color: #f2f2f2;
  --main-font-color: #ffffff;
  --sec-font-color: #4a4a4a;
  --main-theme-color: #27ae60;

  /* font-size */
  --md-font-size: 1.3em;
  --sm-font-size: 0.9em;

  /* font-family */
  --main-font-family: "Roboto Mono", sans-serif;
  --sec-font-family: "Roboto", sans-serif;
}

/* declaration of global variables ends */
/* ********************************************************** */

/* ********************************************************** */
/* root css styling starts */

h1, h2, h3, h4, h5, h6 {
  font-family: "Roboto Mono", sans-serif !important;
  font-family: var(--main-font-family) !important;
}

a {
  color: #ffffff;
  color: var(--main-font-color);
}

a:hover {
  color: #000;
  color: initial;
  text-decoration: none !important;
}

input:focus,
button:focus {
  outline: none !important;
  box-shadow: none !important;
}

/* root css styling ends */
/* ********************************************************** */

/* ********************************************************** */
/* Main styling starts */

.main-sec-div {
  background-color: #f2f2f2;
  background-color: var(--light-bg-color);
  padding: 20px 10px;
}

.main-sec-div {
  color: #4a4a4a;
  color: var(--sec-font-color);
}

.sec-div {
  padding: 30px 0px;
  text-align: center;
}

.sec-head,
.sec-desc,
.sec-content {
  margin: 20px 10px;
  text-align: center;
}

.sec-desc,
.sec-content {
  padding: 20px 10px;
  font-size: 1.3em;
  font-size: var(--md-font-size);
}

.sec-head h1 {
  position: relative;
  font-weight: 600;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0px auto;
  padding: 10px;
}

.sec-head h1::after {
  content: "";
  background-color: #27ae60;
  background-color: var(--main-theme-color);
  position: absolute;
  height: 4px;
  right: 41%;
  bottom: -5px;
  width: 50px;
}

.sec-desc {
  width: 80%;
  margin: 20px auto;
  font-size: 1.3em;
  font-size: var(--md-font-size);
}

.sec-desc p {
  font-family: "Roboto", sans-serif;
  font-family: var(--sec-font-family);
}

.sec-sub-head h2 {
  font-weight: 600;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0px auto;
  padding: 10px;
}


.card-div {
  text-align: center;
  padding: 15px;
}

.card-head h3 {
  position: relative;
}

.card-head h3::after {
  content: "";
  background-color: #27ae60;
  background-color: var(--main-theme-color);
  position: absolute;
  height: 3px;
  right: 41%;
  bottom: -8px;
  width: 50px;
}

.card-desc p{
  font-size: 0.9em;
  font-size: var(--sm-font-size);
}

.card-desc .btn a {
  color: #27ae60;
  color: var(--main-theme-color);
}

.card-desc .btn {
  border: 2px solid #27ae60;
  border: 2px solid var(--main-theme-color);
  border-radius: 20px;
  font-weight: 500;
}

.card-desc .btn:hover {
  background-color: #27ae60;
  background-color: var(--main-theme-color);
}

.card-desc .btn:hover a {
  color: #ffffff;
  color: var(--main-font-color);
  text-decoration: none;
}

.custom {
  color: #27ae60;
  color: var(--main-theme-color);
}

/* Main styling ends */
/* ********************************************************** */

/* ************************************************************************* */

