/********** Small devices only **********/
@media (min-width: 768px) and (max-width: 991px) {
  .card-head h3::after {
    right: 35%;
  }
  .sec-desc {
    width: 80%;
  }
}

/********** Extra small devices only **********/
@media (max-width: 767px) {

  .card-head h3::after {
    right: 43.5%;
  }
  .sec-desc {
    width: 90%;
  }
}

/********** Large Mobile devices only **********/
@media (max-width: 450px) {
  .card-head h3::after {
    right: 42%;
  }
}

/********** Medium Mobile devices only **********/
@media (max-width: 380px) {
  .card-head h3::after {
    right: 40%;
  }
}

