.main-sec-div .sec-content .sec-detail {
  width: 70%;
  margin: auto;
  margin-top: 50px;
}

.explore-btn {
  min-width: 200px;
  margin: auto;
  margin-top: 100px;
}

.explore-btn i {
  color: var(--main-theme-color);
}

.explore-btn .btn {
  padding: 10px 20px;
  font-size: var(--md-font-size);
  font-family: var(--main-font-family);
  font-weight: 500;
  color: var(--main-theme-color);
  border: 2px solid var(--main-theme-color);
  border-radius: 30px;
}

.explore-btn .btn:hover {
  color: var(--main-theme-color);
}

/* Responsive view styling starts */

/***###*** MEDIA QUERIES ***###***/

/********** Large devices only **********/
@media (min-width: 1200px) {
}

/********** Medium devices only **********/
@media (min-width: 992px) and (max-width: 1199px) {
}

/********** Small devices only **********/
@media (min-width: 768px) and (max-width: 991px) {
  .main-sec-div .sec-content .sec-detail {
    width: 80%;
    margin-top: 30px;
  }
}

/********** Extra small devices only **********/
@media (max-width: 767px) {

  .main-sec-div .sec-content .sec-detail {
    width: 100%;
    margin-top: 0px;
  }
  .explore-btn {
    margin-top: 80px;
  }
}

/********** Large Mobile devices only **********/
@media (max-width: 450px) {
}

/********** Medium Mobile devices only **********/
@media (max-width: 380px) {
}

/********** small Mobile devices only (eg: Iphone 4, ) **********/
@media (max-width: 330px) {
  .explore-btn {
    min-width: 180px;
  }

  .explore-btn .btn {
    padding: 5px 6px;
  }
}

/* Responsive view styling ends */
